.SuperButton_default__-BXUG,
.SuperButton_red__1kaz_ {
    margin: 10px 20px;
    padding: 15px 30px;

    width: 150px;

    display: inline-block;

    overflow: hidden;
    border: 2px solid;
    border-bottom-width: 4px;
    outline: none;

    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
    letter-spacing: 2px;
    color: rgb(70, 240, 92);

    background: rgba(0, 0, 0, 1);
    transition: color .3s, background .5s;
}

.SuperButton_default__-BXUG:hover,
.SuperButton_red__1kaz_:hover {
    animation: SuperButton_stripes__AEqOE .75s infinite linear;
    background: linear-gradient(45deg,
    rgb(70, 240, 92) 25%,
    rgba(0, 0, 0, 1) 25%,
    rgba(0, 0, 0, 1) 50%,
    rgb(70, 240, 92) 50%,
    rgb(70, 240, 92) 75%,
    rgba(0, 0, 0, 1) 75%,
    rgba(0, 0, 0, 1));
    background-size: 10px 10px;
    color: white;
    font-size: 15px;
    box-shadow: 0 0 5px #ffffff, 0 0 10px #ffffff, 0 0 20px #00ff00,0 0 90px #00ff00;
}

@keyframes SuperButton_stripes__AEqOE {
    0% {
        background-position: 0 0;
    }
    100% {
        background-position: 30px 0;
    }
}

.SuperButton_red__1kaz_ {
    color: #FF50E5;
    background: rgba(255, 255, 255, 1);
}

.SuperButton_red__1kaz_:hover {
    animation: SuperButton_stripes__AEqOE .75s infinite linear;
    background: linear-gradient(45deg, rgb(234, 187, 187) 25%,
    rgba(255, 255, 255, 1) 25%,
    rgba(255, 255, 255, 1) 50%,
    rgba(234, 187, 187, 1) 50%,
    rgba(234, 187, 187, 1) 75%,
    rgba(255, 255, 255, 1) 75%,
    rgba(255, 255, 255, 1));
    background-size: 10px 10px;
    color: rgba(243, 63, 94, 1);
}

@keyframes SuperCheckbox_click-wave__1j_BQ {
    0% {
        height: 40px;
        width: 40px;

        opacity: 0.35;
        position: relative;
    }
    100% {
        margin-left: -80px;
        margin-top: -80px;

        height: 200px;
        width: 200px;

        opacity: 0;
    }
}

.SuperCheckbox_checkbox__Imn1x {
    margin-right: 0.5rem;

    height: 40px;
    width: 40px;

    position: relative;
    top: 13.33333px;
    right: 0;
    bottom: 0;
    left: 0;

    -webkit-appearance: none;

       -moz-appearance: none;

            appearance: none;

    transition: all 0.15s ease-out 0s;
    background: black;
    outline: none;
    border: 2px solid #00ff00;
    color: black;
    cursor: pointer;
    display: inline-block;

    z-index: 1000;
}

.SuperCheckbox_checkbox__Imn1x:checked {
    background: #00ff00;
}

.SuperCheckbox_checkbox__Imn1x:checked::before {
    content: '✔';

    height: 40px;
    width: 40px;

    position: absolute;

    display: inline-block;

    font-size: 26.66667px;
    text-align: center;
    line-height: 35px;
}

.SuperCheckbox_checkbox__Imn1x:checked::after {
    content: '';

    position: relative;
    animation: SuperCheckbox_click-wave__1j_BQ 0.65s;
    background: #00ff00;
    display: block;
    z-index: 100;
}

.SuperCheckbox_checkbox__Imn1x.SuperCheckbox_radio__h1p-T {
    border-radius: 50%;
}

.SuperCheckbox_checkbox__Imn1x.SuperCheckbox_radio__h1p-T::after {
    border-radius: 50%;
}

.SuperCheckbox_checkbox_block__ASn8B {
    display: block;
    line-height: 40px;
}


